import { thiefJobs } from "@/lib/data/jobs";
import Link from "next/link";

export default function ThiefPage() {
  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
      <div className="mb-8">
        <h1 className="text-4xl font-bold mb-4">도적</h1>
        <p className="text-gray-600 text-lg">
          빠른 속도와 회피로 적을 농락하는 암살자
        </p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {thiefJobs.map((job) => (
          <div
            key={job.id}
            className="bg-white rounded-lg shadow-md p-6 hover:shadow-xl transition-shadow"
          >
            <h3 className="text-2xl font-bold mb-3">{job.name}</h3>
            <p className="text-gray-600 mb-4">{job.description}</p>
            
            <div className="space-y-2">
              <h4 className="font-semibold text-gray-700">육성 가이드</h4>
              <ul className="space-y-1 text-sm">
                <li>
                  <Link href={`/thief/${job.id}/1st`} className="text-blue-600 hover:underline">
                    • 1차 전직 (Lv.10~29)
                  </Link>
                </li>
                <li>
                  <Link href={`/thief/${job.id}/2nd`} className="text-blue-600 hover:underline">
                    • 2차 전직 (Lv.30~69)
                  </Link>
                </li>
                <li>
                  <Link href={`/thief/${job.id}/3rd`} className="text-blue-600 hover:underline">
                    • 3차 전직 (Lv.70~119)
                  </Link>
                </li>
                <li>
                  <Link href={`/thief/${job.id}/4th`} className="text-blue-600 hover:underline">
                    • 4차 전직 (Lv.120+)
                  </Link>
                </li>
              </ul>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
